body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  background-color: #61dafb;
  height: 100vh;
  overflow-y: auto;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  position: fixed;
  z-index: 800;
  width: 100%;
  background-color: #282c34;
  min-height: 5vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.basic-button{
  color: white;
  text-decoration: none;
  background-color: #61dafb;
  border-color: #754fa0;
  border-radius: 2vh;
  font-weight: 600;
  justify-content: center;
}

.App-link {
  color: #61dafb;
}

.App-body{
  position: relative;
  z-index: 800;
  background-color: #61dafb;
  margin-top: 5vh;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.route-wrapper {
  position: relative;
  display: flex;
  justify-content: center;
}

.route-wrapper > div {
  position: absolute;
  display: flex;
  justify-content: center;
}

.headshot-image {
  border-radius: 20px;
  border-color: black;
  border-width: 2px;
  justify-content: center;
}

.headshot-image > img {
  width: 50%;
  margin-top: 20px;
  border-radius: 20px;
  border-color: black;
  border-width: 2px;
  justify-content: center;
}

.message-content {
  display: inline-flex;
  width: 80%;
  height: 100%;
  margin: 10px;
  margin-top: 30px;
  border: 2px solid black;
  border-radius: 40px; 
  color: black; 
  background-color: gray; 
  padding-left: 16px; 
  padding-right: 8px; 
  padding-top: 8px;
}

.headshot {
    height: 20vh;
    width: 20vh;
    border: 5px solid blue;
    border-radius: 5vh;
    margin: 1vh;
    display: inline-block;
    align-content: center;
    position: relative;
}

.name-box{
    opacity: 0;
    display: flex;
    position: relative;
    border-radius: 5vh;
    background-color: #6bbde4;
    z-index: 200;
    width: 100%;
    height: 100%;
    -webkit-animation: fadeOut .5s ease-in-out;
            animation: fadeOut .5s ease-in-out;
}

.image-box{
    display: flex;
    position: relative;
    border-radius: 5vh;
    background-color: #6bbde4;
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
    z-index: 100;
    width: 100%;
    height: 100%;
}

.invisible{
    visibility: hidden;
}

.name-box:hover{
    display: flex;
    background-color: #6bbde4;
    z-index: 200;
    width: 100%;
    height: 100%;
    opacity: 1;
    -webkit-animation: fadeIn .25s ease-in-out;
            animation: fadeIn .25s ease-in-out;
}

.name-focus{
    opacity: 0;
    color: blue;
    position: relative;
    width: 100%;
    height: 100%;
    z-index: 300;
    -webkit-animation: fadeOut .25s ease-in-out;
            animation: fadeOut .25s ease-in-out;
}

.name-focus:hover{
    width: 100%;
    height: 100%;
    opacity: 1;
    z-index: 300;
    -webkit-animation: fadeIn .5s ease-in-out;
            animation: fadeIn .5s ease-in-out;
}

@-webkit-keyframes fadeOut {
    0%{
    opacity: 1;
    }
    40%{
    opacity: .6;
    }
    100% {
    opacity: 0;
    }
    20% {
    opacity: .8;
    }
    60%{
    opacity: .4;
    }
    80%{
    opacity: .2;
    }
}

@keyframes fadeOut {
    0%{
    opacity: 1;
    }
    40%{
    opacity: .6;
    }
    100% {
    opacity: 0;
    }
    20% {
    opacity: .8;
    }
    60%{
    opacity: .4;
    }
    80%{
    opacity: .2;
    }
}

@-webkit-keyframes fadeIn {
    0%{
    opacity: 0;
    }
    40%{
    opacity: .4;
    }
    100% {
    opacity: 1;
    }
    20% {
    opacity: .2;
    }
    60%{
    opacity: .6;
    }
    80%{
    opacity: .8;
    }
}

@keyframes fadeIn {
    0%{
    opacity: 0;
    }
    40%{
    opacity: .4;
    }
    100% {
    opacity: 1;
    }
    20% {
    opacity: .2;
    }
    60%{
    opacity: .6;
    }
    80%{
    opacity: .8;
    }
}
